.drug {

    &__content {
        display: flex;
        justify-content: center;
        background: var(--color-athens-gray);
        border-radius: 30px;

        @include resolution(small-desktop) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__image {
        margin: 80px 0;

        @include resolution(small-desktop) {
            width: 600px;
            margin-right: 100px;
        }

        @include resolution(big-tablet) {
            width: 560px;
        }

        @include resolution(tablet) {
            width: 90%;
            margin-right: 10%;
        }
    }

    &__info {
        max-width: 540px;
        margin-top: 120px;
        margin-left: 30px;

        @include resolution(small-desktop) {
            margin-top: 60px;
            margin-left: 0;
        }

        @include resolution(tablet) {
            max-width: 80%;
        }

        @include resolution(big-mobile) {
            margin-top: 0;
        }
    }

    &__title {
        @include font(play);
        font-size: 24px;
        line-height: 32px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: var(--color-ebony-clay);

        @include resolution(big-mobile) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__list {
        margin-top: 15px;
        margin-left: 22px;
        list-style-image: url('./../assets/icons/arrow.svg');

        @include resolution(small-desktop) {
            margin-bottom: 60px;
        }
    }

    &__item {
        margin-bottom: 10px;
        @include font(raleway);
        font-size: 20px;
        line-height: 32px;
        letter-spacing: 1.2px;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: var(--color-ebony-clay);

        span {
            margin-left: 12px;
            @include font(raleway-bold);
        }

        &:nth-child(3) {
            margin-bottom: 0;
        }

        @include resolution(big-mobile) {
            line-height: 24px;
        }
    }
}